body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#parallax-space {
    /* The image used */
    background-image: url(/portfolio-site/static/media/blue-orange-space.3e8c7358.jpg);
    /* Set a specific height */
    height: 700px;
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

#parallax-nash {
    /* The image used */
    background-image: url(/portfolio-site/static/media/nash.f6d89a3c.jpg);
    /* Set a specific height */
    height: 700px;
    opacity: .80;
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: left;
    background-repeat: no-repeat;
    background-size: cover;
}

#header-div {
    display: flex;
    justify-content: center;
    flex-direction: column;
    /* The image used */
    background-image: url(/portfolio-site/static/media/Sacramento_Skyline.27c4218c.jpg);
    opacity: .80;
    /* Set a specific height */
    height: 900px;
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: left;
    background-repeat: no-repeat;
    background-size: cover;
}

#header-div h2 {
    margin: .5rem 2rem .5rem 2rem;
    font-size: 10em;
}

#header-div h4 {
    margin: 1rem 2rem 1rem 2rem;
}
#nav-link-div {
    font-size: 1.5em;
    display: flex;
}

#hello-div {
    width: 100%;
    color: rgb(0, 37, 94);
    background-color: #66ace6;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

#about-me {
    margin: 15rem 2rem 15rem 2rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

#about-me h2 {
    font-size: 3.5em;
}
#about-me p {
    font-size: 1.5em;
}

#project-div {
    margin: 15rem 2rem 15rem 2rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

#project-div h2 {
    font-size: 3.5em;
}

#tech-used-div {
    margin: 10rem 2rem 10rem 2rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

#tech-used-div h2{
    font-size: 3.5em;
}
